@font-face {
  font-family: "Raleway Black";
  src: url("../fonts/ralewayblack.woff") format("woff"), url("../fonts/ralewayblack.woff2") format("woff2");
  font-style: normal;
  font-weight: 500; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/ralewaybold.woff") format("woff"), url("../fonts/ralewaybold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700; }

@font-face {
  font-family: "Roboto Medium";
  src: url("../fonts/robotomedium.woff") format("woff"), url("../fonts/robotomedium.woff2") format("woff2");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff") format("woff"), url("../fonts/roboto.woff2") format("woff2");
  font-style: normal;
  font-weight: 300; }

*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none; }

ul, li {
  padding: 0;
  margin: 0; }

li {
  list-style: none; }

body {
  padding: 0;
  margin: 0 auto;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400; }

.btn {
  padding: 14px 30px;
  background: #00ffd8;
  font-size: 15px;
  line-height: 18px;
  color: #000000; }

.page-header {
  background-color: #fbfbfb; }

.page-header__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 18px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.page-header__logo-link {
  width: 170px; }

.page-header__contacts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px; }

.page-header__contacts-phone {
  color: #000000;
  padding-right: 25px; }
  .page-header__contacts-phone:hover {
    opacity: 0.6; }

.page-header__contacts-feedback:hover {
  opacity: 0.6; }

.main-nav {
  margin-right: 200px; }

.main-nav__list {
  display: flex;
  flex-direction: row;
  padding: 15px 0; }

.main-nav__list-item {
  list-style: none;
  padding-left: 20px; }

.main-nav__link {
  color: #838383;
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-size: 10px;
  line-height: 20px;
  text-transform: uppercase; }
  .main-nav__link:hover {
    color: #000000; }

.banner__wrapper {
  width: 1170px;
  margin: 0 auto;
  display: flex;
  flex-direction: column; }

.banner {
  background-image: url(../../img/main-bg.jpg);
  background-position: center;
  background-color: #000000;
  background-size: 100%;
  background-repeat: no-repeat;
  padding: 50px 0; }

.banner__title {
  font-family: "Raleway Black", Arial, sans-serif;
  font-size: 72px;
  line-height: 80px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 4px;
  margin: 40px 0 15px; }

.banner__description {
  width: 935px;
  font-family: "Roboto Medium", Arial, sans-serif;
  font-size: 22px;
  line-height: 36px;
  color: #ffffff;
  margin-bottom: 35px; }
  .banner__description span {
    color: #00ffd8; }

.banner__btns {
  display: flex;
  flex-direction: row;
  width: 620px; }

.banner__btn {
  padding-top: 18px;
  margin-bottom: 30px;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  width: 300px; }
  .banner__btn--blank {
    margin-left: 20px;
    background: none;
    border: 1px solid #00ffd8;
    color: #ffffff; }

.banner__features {
  padding-top: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.banner__features-item {
  display: flex;
  flex-direction: column;
  color: #b0afb0;
  width: 200px;
  font-size: 15px;
  line-height: 20px;
  padding-top: 80px;
  text-align: center; }
  .banner__features-item--guarantee {
    background-image: url(../../img/like.png);
    background-size: 64px 64px;
    background-position: top;
    background-repeat: no-repeat; }
  .banner__features-item--timing {
    background-image: url(../../img/handshake.png);
    background-size: 74px 49px;
    background-position: top;
    background-repeat: no-repeat; }
  .banner__features-item--factory {
    background-image: url(../../img/settings.png);
    background-size: 70px 69px;
    background-position: top;
    background-repeat: no-repeat; }

.page-main__wrapper {
  width: 1170px;
  margin: 0 auto; }

.services {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 40px 0;
  justify-content: space-between; }

.services__title {
  font-family: "Raleway Black", Arial Black, sans-serif;
  font-size: 30px;
  line-height: 50px;
  display: block;
  width: 100%;
  position: relative;
  color: #000000;
  text-transform: uppercase; }
  .services__title::after {
    content: "";
    display: block;
    width: 877px;
    height: 2px;
    background: #000000;
    position: absolute;
    left: 25%;
    top: 25px; }

.services__item {
  width: 360px;
  padding: 25px 46px 50px;
  margin-bottom: 50px;
  box-shadow: 1px 5px 10px #f3f3f3; }

.services__item-title {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 16px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase; }

.services__item-element {
  font-size: 13px;
  line-height: 16px;
  color: #737373;
  padding-bottom: 10px; }

.projects__title {
  font-family: "Raleway Black", Arial Black, sans-serif;
  font-size: 30px;
  line-height: 50px;
  display: block;
  width: 100%;
  position: relative;
  color: #000000;
  text-transform: uppercase; }
  .projects__title::after {
    content: "";
    display: block;
    width: 877px;
    height: 2px;
    background: #000000;
    position: absolute;
    left: 25%;
    top: 25px; }
